"use client";

import React from 'react';
import Link from 'next/link';
import { Button } from './ui/Button';
import { useAuth } from '../context/AuthContext';
import { LogIn, User, HeartPulse } from 'lucide-react';

export function Header() {
  const { user } = useAuth();

  return (
    <header className="bg-white border-b border-gray-200">
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          <Link href="/" className="text-2xl font-bold text-primary-600 flex items-center">
            <HeartPulse className="h-8 w-8 mr-2" />
            Vital
          </Link>

          <nav>
            {user ? (
              <div className="flex items-center space-x-6">
                <Link href="/dashboard" className="text-gray-600 hover:text-gray-900">
                  Dashboard
                </Link>
                <Link href="/requests" className="text-gray-600 hover:text-gray-900">
                  Requests
                </Link>
                <Link href="/requests/my-requests" className="text-gray-600 hover:text-gray-900">
                  My Requests
                </Link>
                <Link href="/donations" className="text-gray-600 hover:text-gray-900">
                  Donations
                </Link>
                <Link href="/nearby-donors" className="text-gray-600 hover:text-gray-900">
                  Find Donors
                </Link>
                {user.role === 'admin' && (
                  <Link href="/admin" className="text-red-600 font-medium hover:text-red-700">
                    Admin Console
                  </Link>
                )}
                <Link href="/profile">
                  <Button variant="ghost" className="p-2">
                    <User className="h-5 w-5" />
                  </Button>
                </Link>
              </div>
            ) : (
              <Link href="/login">
                <Button variant="ghost" className="flex items-center space-x-2">
                  <LogIn className="h-5 w-5" />
                  <span>Login</span>
                </Button>
              </Link>
            )}
          </nav>
        </div>
      </div>
    </header>
  );
}